import {NgModule} from "@angular/core";
import {CommonModule} from "@angular/common";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {ClarityModule} from "@clr/angular";
import {CommonPipeModule} from "@pipe/common-pipe.module";
import {OwlDateTimeIntl, OwlDateTimeModule, OwlNativeDateTimeModule, OWL_DATE_TIME_FORMATS} from "ng-pick-datetime";
import {DatetimePickerComponent} from "@component/datetime-picker/datetime-picker.component";
import {LocalizePickerLabels, pickerFormats} from "@component/datetime-picker/localize-picker-labels";
import {DateRangePickerComponent} from "@component/datetime-picker/date-range-picker.component";
import {AddTimeRangeDialogComponent} from './time-range-picker/add-time-range-dialog.component';
import {DeleteRangeConfirmDialogComponent} from "@component/datetime-picker/time-range-picker/delete-range-confirm-dialog.component";
import {VsanTimeRangePickerComponent} from "@component/datetime-picker/time-range-picker/time-range-picker.component";
import {PromptModule} from "@component/dialog/prompt/prompt.module";
import {RouterModule, Routes} from "@angular/router";
import {LastFieldDatePickerComponent} from "@component/datetime-picker/time-last-field-picker/last-field-date-picker.component";

const routes: Routes = [
   {
      path: "addTimeRange",
      component: AddTimeRangeDialogComponent,
   },
   {
      path: "deleteTimeRange",
      component: DeleteRangeConfirmDialogComponent,
   },
];

@NgModule({
   imports: [
      CommonModule,
      FormsModule,
      ReactiveFormsModule,
      ClarityModule,
      CommonPipeModule,
      PromptModule,
      OwlDateTimeModule,
      OwlNativeDateTimeModule,
      RouterModule.forChild(routes),
   ],
   declarations: [
      DatetimePickerComponent,
      DateRangePickerComponent,
      VsanTimeRangePickerComponent,
      AddTimeRangeDialogComponent,
      DeleteRangeConfirmDialogComponent,
      LastFieldDatePickerComponent,
   ],
   exports: [
      DatetimePickerComponent,
      DateRangePickerComponent,
      VsanTimeRangePickerComponent,
      LastFieldDatePickerComponent,
   ],
   providers: [
      {provide: OwlDateTimeIntl, useClass: LocalizePickerLabels},
      {provide: OWL_DATE_TIME_FORMATS, useValue: pickerFormats},
   ],
})
export class DatetimePickerModule {
}