import {Component, OnInit, ViewChild} from "@angular/core";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn
} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {ManagedObject} from "@service/managed-object";
import {PerfTimeRangeData} from "@generated/perf-time-range-data";
import {VsanPerfMutationProvider} from "@generated/vsan-perf-mutation-provider";
import {VsanUiUtils} from "@util/vsan-util";
import {Logger} from "@util/logger";
import {DateFormat} from "@pipe/VsanDateFormatterPipe";

@Component({
   selector: "vsan-add-time-range-dialog",
   templateUrl: "add-time-range-dialog.html",
   styleUrls: ["./add-time-range-dialog.scss"],
})

export class AddTimeRangeDialogComponent implements OnInit {
   readonly NAME_CONTROL_NAME: string = "name";
   readonly NAME_VALIDATION: string = "nameValidation";

   from: number;
   to: number;
   busy: boolean;
   alertMessages: string[] = [];
   timeRangeSettings: FormGroup;
   DateFormat = DateFormat;

   @ViewChild(ClrForm, {static: true})
   timeRangeForm;

   private clusterRef: ManagedObject;

   constructor(private formBuilder: FormBuilder,
         private perfMutationProvider: VsanPerfMutationProvider) {
      this.timeRangeSettings = this.formBuilder.group({
         [this.NAME_CONTROL_NAME]: new FormControl("", [this.nameValidator])
      });
   }

   ngOnInit() {
      const context: any = VsanUiUtils.getModalContext();
      this.clusterRef = context.clusterRef;
      this.from = context.from;
      this.to = context.to;
   }

   create = () => {
      if (this.timeRangeSettings.invalid) {
         // Verify inputs when user opens the dialog and click Create button directly.
         this.timeRangeForm.markAsTouched();
         return;
      }

      this.busy = true;
      const range: PerfTimeRangeData =
            PerfTimeRangeData.Factory.create(this.getName(), this.from, this.to, this.clusterRef);
      this.perfMutationProvider.setTimeRanges(range).then(() => {
         this.busy = false;
         VsanUiUtils.closeModalDialog(range);
      }).catch((err) => {
         this.busy = false;
         Logger.error("Failed to save the time range, ", err);
         this.alertMessages.push(VsanUiUtils.getString("vsan.perf.chart.timerange.save.error.msg"));
      });
   };

   private nameValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      if (!this.getName() || this.getName().length > 256) {
         return {
            [this.NAME_VALIDATION]: {value: control.value}
         };
      }
      return null;
   };

   get blockCreateButton(): boolean {
      return this.busy || (this.timeRangeSettings && this.timeRangeSettings.touched && this.timeRangeSettings.invalid);
   }

   private getName(): string {
      return this.timeRangeSettings ?
            this.timeRangeSettings.get(this.NAME_CONTROL_NAME).value :
            null;
   }
}