import {Pipe, PipeTransform} from "@angular/core";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {DateFormat} from "@util/date-time";
export {DateFormat};

@Pipe({
   name: "l10nDate",
})
export class VsanDateFormatterPipe implements PipeTransform {

   transform(value: Date | number, format: DateFormat = DateFormat.FULL_DATE_TIME): string {
      return VsanDateTimeUtils.applyFormatting(value, format);
   }
}