import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {PerfTimeRangeData} from "@generated/perf-time-range-data";
import {VsanPerfMutationProvider} from "@generated/vsan-perf-mutation-provider";
import {VsanUiUtils} from "@util/vsan-util";
import {Logger} from "@util/logger";
import {DateFormat} from "@pipe/VsanDateFormatterPipe";

@Component({
   selector: "vsan-delete-range-confirm-dialog",
   templateUrl: "delete-range-confirm-dialog.html",
   styleUrls: ["./delete-range-confirm-dialog.scss"],
})

export class DeleteRangeConfirmDialogComponent implements OnInit {

   clusterRef: ManagedObject;
   range: PerfTimeRangeData;
   busy: boolean;
   alertMessages: string[] = [];
   DateFormat = DateFormat;

   constructor(private perfMutationProvider: VsanPerfMutationProvider) {
   }

   ngOnInit() {
      this.clusterRef = VsanUiUtils.getModalContext().clusterRef;
      this.range = VsanUiUtils.getModalContext().range;
   }

   apply() {
      this.alertMessages = [];
      this.busy = true;
      this.perfMutationProvider.deleteTimeRange(this.clusterRef, this.range).then(() => {
         this.busy = false;
         VsanUiUtils.closeModalDialog(this.range);
      }).catch((err) => {
         this.busy = false;
         Logger.error("Failed to delete a time range, ", err);
         this.alertMessages.push(VsanUiUtils.getString("vsan.perf.chart.timerange.del.error.msg"));
      });
   }
}